.preset-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.preset-dropdown {
  position: relative;
  display: inline-block;
}

.preset-options {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
  border: 1px solid #ccc;
  padding: 5px;
  margin-top: 2px;
}

.preset-dropdown:hover .preset-options {
  display: block;
}

.preset-options .btn {
  margin: 2px 0;
  width: 100%;
}